import React, { useState } from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";
import { useNavigate } from "react-router-dom";
import * as Yup from "yup";
import axios from "axios";
import Footer from "../component/footer";

const HomePage = () => {
  const history = useNavigate();
  const inputClass = "rounded-md bg-zinc-100 p-3 w-full";
  const [fileInputs, setFileInputs] = useState([1]);
  const [files, setFiles] = useState([]);

  const addFileInput = () => {
    setFileInputs([...fileInputs, fileInputs.length + 1]);
    setFiles([...files, null]);
  };

  //          
  const securityRegExp =
    /<\s*(\b(script|iframe|embed|object|frameset|frame|applet)\b)[^>]*>.*?<\/\1>|on\w+\s*=|javascript\s*:/gi;
  const securityMessage = "    .";

  //   
  const emailRegExp =
    /^[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/i;
  const emailMessage = "   .";

  const validateFile = (file) => {
    if (!file) return false; //     

    //   
    const fileExtension = file.name.split(".").pop().toLowerCase();
    const isExecutable = /\.(exe|dll|bat|cmd)$/i.test(fileExtension);
    if (isExecutable) {
      return false; //     
    }

    //    (100MB )
    const maxSize = 100 * 1024 * 1024; // 100MB
    const isOverSize = file.size > maxSize;
    if (isOverSize) {
      return false; // 100MB     
    }

    return true; //    
  };

  //          
  const maxLength = 200;

  //   
  const validationSchema = Yup.object().shape({
    company: Yup.string()
      .max(maxLength, `  ${maxLength}   .`)
      .matches(/^(?![\s\S]*<.*>).{0,200}$/, securityMessage),
    q_position: Yup.string()
      .max(maxLength, `  ${maxLength}   .`)
      .matches(/^(?![\s\S]*<.*>).{0,200}$/, securityMessage),
    q_name: Yup.string()
      .max(maxLength, `  ${maxLength}   .`)
      .matches(/^(?![\s\S]*<.*>).{0,200}$/, securityMessage)
      .required("."),
    call_num: Yup.string()
      .max(maxLength, `  ${maxLength}   .`)
      .matches(/^(?![\s\S]*<.*>).{0,200}$/, securityMessage)
      .required("."),
    email: Yup.string()
      .max(maxLength, `  ${maxLength}   .`)
      .matches(emailRegExp, emailMessage),
    target: Yup.string()
      .max(maxLength, `  ${maxLength}   .`)
      .matches(/^(?![\s\S]*<.*>).{0,200}$/, securityMessage),
    sample: Yup.string()
      .max(maxLength, `  ${maxLength}   .`)
      .matches(/^(?![\s\S]*<.*>).{0,200}$/, securityMessage),
    memo: Yup.string()
      .max(maxLength, `  ${maxLength}   .`)
      .matches(/^(?![\s\S]*<.*>).{0,200}$/, securityMessage)
      .required("."),

    files: Yup.array().of(
      Yup.mixed().test("file", "  .", (value) => {
        return value ? validateFile(value) : true;
      })
    ),
  });

  const handleFileChange = (event, index) => {
    const selectedFile = event.target.files[0];
    const newFiles = [...files];
    newFiles[index] = selectedFile;
    setFiles(newFiles);
  };

  return (
    <div className="break-keep">
      <div className="svisual_02 flex justify-center text-white font-bold h-40 text-2xl pt-20 lg:pt-40 lg:h-96 lg:text-6xl">
        
      </div>
      <div className="flex flex-col items-center justify-center lg:p-20 p-8 pt-10 pb-10">
        <div className="lg:w-8/12 text-center">
          <p className="font-bold text-4xl lg:text-3.5vw text-center">
            
            <br className="lg:hidden" />
            <span className="text-c1">Quick Survey</span><br />
              ?
          </p>
          <p className="lg:text-1vw text-zinc-500 lg:pt-6 lg:pb-8 text-center leading-tight pt-4 pb-4">
                  .
          </p>

          <div className="rounded-xl bg-zinc-50 p-8 mb-8 text-left">
            <p className="flex items-center font-bold text-md pb-4">
              <input
                type="checkbox"
                className="bg-c1 text-white w-6 h-6 mr-2 rounded-md"
              ></input>{" "}
                  ()
            </p>
            <p className="lg:h-auto h-[100px] overflow-auto text-zinc-500 lg:text-1vw">
                 ,  .  
                 .
              <br />
              : , , , , 
              <br />
              :    
              <br />
                 : 1
              <br />
                     . , 
                       .
            </p>
          </div>
          <p className="font-bold text-left w-full pb-4">
            <span className="text-c1">*</span>  .
          </p>

          <Formik
            initialValues={{
              company: "",
              q_position: "",
              q_name: "",
              call_num: "",
              email: "",
              target: "",
              sample: "",
              memo: "",
            }}
            validationSchema={validationSchema}
            onSubmit={(values, { setSubmitting }) => {
              setSubmitting(true);
              const formDataToSend = new FormData();

              formDataToSend.append("company", values.company);
              formDataToSend.append("q_position", values.q_position);
              formDataToSend.append("q_name", values.q_name);
              formDataToSend.append("call_num", values.call_num);
              formDataToSend.append("email", values.email);
              formDataToSend.append("target", values.target);
              formDataToSend.append("sample", values.sample);
              formDataToSend.append("memo", values.memo);

              files.forEach((file, index) => {
                formDataToSend.append(`file${index + 1}`, file);
              });

              console.log("Form Data to Send:", {
                company: values.company,
                q_position: values.q_position,
                q_name: values.q_name,
                call_num: values.call_num,
                email: values.email,
                target: values.target,
                sample: values.sample,
                memo: values.memo,
                files: files,
              });

              console.log("Submitting to backend...");
              // axios.post      .      URL  .
              axios
                .post("https://61.78.75.166:8443/api/data", formDataToSend, {
                  withCredentials: true,
                  headers: {
                    "Content-Type": "multipart/form-data",
                  },
                })
                .then((response) => {
                  console.log(":", response.data);
                  history("/complete");
                })
                .catch((error) => {
                  console.error(":", error);
                })
                .finally(() => {
                  setSubmitting(false);
                  console.log("  ");
                });

              //    setTimeout    .
              setTimeout(() => {
                console.log("Submission successful!");
                setSubmitting(false);
              }, 1000);
            }}
          >
            {({ isSubmitting }) => (
              <Form method="post">
                <div className="rounded-xl border border-c-zinc-200 p-8 lg:p-10 text-left">
                  <ul className="grid grid-cols-2 gap-8 font-bold">
                    <li className="lg:col-span-1 col-span-2">
                      <p className="pb-2">(  ,  )</p>
                      <Field
                        type="text"
                        name="company"
                        className={inputClass}
                      />
                      <ErrorMessage
                        name="company"
                        component="div"
                        className="text-red-500"
                      />
                    </li>
                    <li className="lg:col-span-1 col-span-2">
                      <p className="pb-2">(, ,  )</p>
                      <Field
                        type="text"
                        name="q_position"
                        className={inputClass}
                      />
                      <ErrorMessage
                        name="q_position"
                        component="div"
                        className="text-red-500"
                      />
                    </li>
                    <li className="lg:col-span-1 col-span-2">
                      <p className="pb-2">
                        <span className="text-c1 pr-2">*</span>
                      </p>
                      <Field type="text" name="q_name" className={inputClass} />
                      <ErrorMessage
                        name="q_name"
                        component="div"
                        className="text-red-500"
                      />
                    </li>
                    <li className="lg:col-span-1 col-span-2">
                      <p className="pb-2">
                        <span className="text-c1 pr-2">*</span>
                      </p>
                      <Field
                        type="text"
                        name="call_num"
                        className={inputClass}
                      />
                      <ErrorMessage
                        name="call_num"
                        component="div"
                        className="text-red-500"
                      />
                    </li>
                    <li className="col-span-2">
                      <p className="pb-2"></p>
                      <Field
                        type="text"
                        name="email"
                        placeholder=": email@email.com (@     .)"
                        className={inputClass}
                      />
                      <ErrorMessage
                        name="email"
                        component="div"
                        className="text-red-500"
                      />
                    </li>
                    <li className="col-span-2">
                      <p className="pb-2"></p>
                      <Field
                        type="text"
                        name="target"
                        className="rounded-md bg-zinc-100 p-3 w-full h-28"
                      />
                      <ErrorMessage
                        name="target"
                        component="div"
                        className="text-red-500"
                      />
                    </li>
                    <li className="col-span-2">
                      <p className="pb-2"></p>
                      <Field type="text" name="sample" className={inputClass} />
                      <ErrorMessage
                        name="sample"
                        component="div"
                        className="text-red-500"
                      />
                    </li>
                    <li className="col-span-2">
                      <p className="pb-2">
                        <span className="text-c1 pr-2">*</span>   
                      </p>
                      <Field
                        type="text"
                        name="memo"
                        className="rounded-md bg-zinc-100 p-3 w-full h-28"
                      />
                      <ErrorMessage
                        name="memo"
                        component="div"
                        className="text-red-500"
                      />
                    </li>
                    <li className="col-span-2">
                      <p className="pb-2">
                         (,  ){" "}
                        <span className="text-c1 text-sm block lg:inline">
                          {" "}
                          ()       
                           .
                        </span>
                      </p>
                      <ErrorMessage
                        name="files"
                        component="div"
                        className="text-red-500"
                      />
                      <div>
                        {fileInputs.map((input, index) => (
                          <div key={index}>
                            <Field
                              name={`files[${index}]`}
                              type="file"
                              className={`${inputClass} mb-2`}
                              onChange={(event) =>
                                handleFileChange(event, index)
                              }
                            />
                            {index === fileInputs.length - 1 && (
                              <p
                                className="font-bold text-zinc-900 text-right pt-4 text-sm pr-4 cursor-pointer"
                                onClick={addFileInput}
                              >
                                {" "}
                                + 
                              </p>
                            )}
                            <ErrorMessage
                              name={`files[${index}]`}
                              component="div"
                              className="text-red-500"
                            />
                          </div>
                        ))}
                      </div>
                    </li>
                  </ul>
                </div>
                <p className="pt-4 pb-4 text-sm">
                      ,   
                    .
                  <br />
                     {" "}
                  <span className="text-orange-600">
                    (arpark@hrc.co.kr)
                  </span>
                    .
                </p>

                <button
                  type="submit"
                  disabled={isSubmitting}
                  className="bg-c1 p-4 pl-20 pr-20 font-bold text-white text-xl rounded-full mt-2"
                >
                  {isSubmitting ? " ..." : ""}
                </button>
              </Form>
            )}
          </Formik>
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default HomePage;
